import React from "react";
import { useDispatch, useSelector } from "react-redux";
import MainTrading from "../main_trading/mainTrading";

const Test = (props) => {
  const state = useSelector((state) => state.main);
  if (state.isLoading === true)
    return (
      <section>
        <div>로딩중</div>
      </section>
    );
  return (
    <div>
      <h2>TEst</h2>
      <MainTrading />
    </div>
  );
};

export default Test;
